import React from "react";
import { Redirect, useLocation } from "react-router-dom";

function getMobileOperatingSystem() {
  var userAgent = navigator.userAgent || navigator.vendor;

  if (
    userAgent.match(/iPad/i) ||
    userAgent.match(/iPhone/i) ||
    userAgent.match(/iPod/i)
  ) {
    return "iOS";
  } else if (userAgent.match(/Android/i)) {
    return "Android";
  } else {
    return "unknown";
  }
}

const SharePage = () => {
  const useQuery = () => {
    return new URLSearchParams(useLocation().search);
  };
  const query = useQuery();

  const orderId = query.get("orderId");

  React.useEffect(() => {
    const system = getMobileOperatingSystem();

    window.alert(JSON.stringify(system));

    if (system === "Android" && orderId) {
      window.location.href = `atm://orders?orderId=${orderId}`;
    }
    if (system === "iOS" && orderId) {
      window.location.href = `atm://orders?orderId=${orderId}`;
    }
  }, [orderId]);

  React.useEffect(() => {
    window.alert(JSON.stringify(orderId));
  }, [orderId]);

  return <Redirect to={`atm://orders?orderId=${orderId}`} />;
};

export default SharePage;
